import { Button, Text,View } from "react-native";
import {useState} from 'react'
export default function App(){
  const [data,setData] = useState({name:'Vishwa'})
  const handlePress = () =>{
    fetch('http://10.0.2.2:5000/user')
    .then(res=>res.json())
    .then(res=>setData(res))
  }

 return (<View>
  <Text testID="hello">Hello</Text>
  <Button onPress={handlePress} title="press" testID="press" ></Button>
   <Text testID="output">{data.name}</Text>
 </View>)
}